import React, { Fragment, useState, useEffect } from 'react';
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import clsx from 'clsx';
import { Button } from '@material-ui/core';
import PropTypes from "prop-types";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Autocomplete from '@material-ui/lab/Autocomplete';
import { updateUser, listCustomers, createProject } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
const ProjectForm = props => {

  useEffect(() => {
    console.log(props)

    listCustomers().then(response => {
      setCustomers(response.customers);
    });
    if (props.person) {

      // setName(props.person.name);
      // setDocumentNumber(props.person.document_number);
      // setUsername(props.person.username);
      // setRole(props.person.role);
    }
  }, [])

  const [type, setType] = useState('')
  const [disabled, setDisabled] = useState(false)

  const [file, setFile] = useState({})
  const classes = useStyles()

  const [customers, setCustomers] = useState([]);
  const [customer, setCustomer] = useState();
  const [description, setDescription] = useState('');
  const [documentNumber, setDocumentNumber] = useState('');
  const [username, setUsername] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');
  const [role, setRole] = useState(null);

  const [errorDescription, setErrorDescription] = useState(false)
  const [errorTextDescription, setErrorTextDescription] = useState('')

  const [errorDocument, setErrorDocument] = useState(false)
  const [errorTextDocument, setErrorTextDocument] = useState('')

  const onChangeDescription = e => {
    setDescription(e.target.value);
    validateDescription(e.target.value);
  }

  const validateForm = () => {
    let errors = 0
    errors += validateDescription()
    // errors += validatePrice()
    // errors += validateQuantity()
    return errors
  }

  const validateDescription = (value) => {
    let text = value !== undefined ? value : description

    if (text == null || text.toString().trim() === '') {
      setErrorDescription(true)
      setErrorTextDescription('Este campo es requerido')
      return 1
    }
    else {
      setErrorDescription(false)
      setErrorTextDescription('')
      return 0
    }
  }

  const validateDocument = (value) => {
    let text = value !== undefined ? value : documentNumber

    if (text == null || text.toString().trim() === '') {
      setErrorDocument(true)
      setErrorTextDocument('Este campo es requerido')
      return 1
    }
    else {
      setErrorDocument(false)
      setErrorTextDocument('')
      return 0
    }
  }

  const onChangeType = e => {
    setType(e.target.value)
  }

  const onSubmitPerson = _ => {

    let errores = validateForm()
    // errores += validateType();


    if (errores === 0) {

      let data = {
        'name': description,
        'type': type,
        'customer': customer._id,
      }

      console.log(data)

      if (!props.person) {
        createProject(data).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Proyecto registrado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          } else {
            toast.error('Ocurri贸 un error al registrar el proyecto', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });

      } else {

        updateUser(data).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Proyecto registrado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          } else {
            toast.error('Ocurri贸 un error al registrar el proyecto', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });
      }
    } else {
      toast.error(' Wow so easy!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmitPerson()
            }} >
            <Grid container spacing={3}>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  select
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Tipo"
                  margin="dense"
                  value={type}
                  onChange={onChangeType}
                >
                  <MenuItem key={"tri"} value={"tri"}>Trif谩sico</MenuItem>
                  <MenuItem key={"mono"} value={"mono"}>Monof谩sico</MenuItem>
                </TextField>
              </Grid>

              <Grid item xs={12} sm={8}>
                <Autocomplete
                  fullWidth
                  size="small"
                  getOptionLabel={
                    (option) => {
                      return `${option.name.toString().toUpperCase()} `
                    }
                  }
                  filterOptions={(x) => x}
                  options={customers}
                  autoComplete
                  getOptionSelected={(option, value) => option === value}
                  onChange={(_, value) => {
                    setCustomer(value);
                  }}
                  onInputChange={(_, newInputValue) => {
                    console.log('newInputValue', newInputValue)
                    if (newInputValue.toString().trim().length > 0) {
                      let data = {
                        search: newInputValue.toString().trim()
                      }
                      listCustomers(data).then(response => {
                        setCustomers(response.customers);
                      })
                    }
                  }}
                  renderInput={(params) => (
                    <TextField {...params} label="Cliente" variant="outlined" margin="dense" />
                  )}
                  renderTags={(tagValue, getTagProps) =>
                    tagValue.map((option, index) => (
                      <Chip
                        label={option.name}
                        {...getTagProps({ index })}
                        disabled={customers.indexOf(option) !== -1}
                      />
                    ))
                  }
                  renderOption={(option) => {

                    return (
                      <Grid container alignItems="center">
                        <Grid item xs>
                          <Typography >{`${option.name.toString().toUpperCase()}`}</Typography>
                        </Grid>
                      </Grid>
                    );
                  }}
                />
              </Grid>

              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  error={errorDescription}
                  helperText={errorTextDescription}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Descripci贸n"
                  value={description}
                  onChange={onChangeDescription} />
              </Grid>




              <Grid item xs={2} sm={3}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>
                  {props.person ? "Actualizar" : "Guardar"}
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default ProjectForm