import React, { useState, useEffect, Fragment } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';

import Chip from '@material-ui/core/Chip';
import config from '../../config';
import './projects.css';
import { Zoom } from "react-slideshow-image";

import 'react-slideshow-image/dist/styles.css';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import Slide from '@material-ui/core/Slide';

import ImageDetail from './ImageDetail';
import { BiShowAlt } from 'react-icons/bi';
import { AiOutlineMail } from 'react-icons/ai';

import {
  Card,
  CardHeader,
  CardContent,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Button,
} from '@material-ui/core';

import { openItemBoard, onSendEmailBoard } from '../../api/api';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
})

const AttentionDetail = props => {

  const [project, setProject] = useState(null);
  const [boards, setBoards] = useState([]);
  const [type, setType] = useState("");
  const [board, setBoard] = useState(null);
  const [cellsBefore, setCellsBefore] = useState([]);
  const [cellsVoltaje, setCellsVoltaje] = useState([]);
  const [cellsCorriente, setCellsCorriente] = useState([]);
  const [cellsAfter, setCellsAfter] = useState([]);
  const [openDetailImage, setOpenDetailImage] = useState(false);
  const [url, setUrl] = useState('');

  useEffect(() => {
    setProject(props.project);
    setType(props.project.type);
    setBoards(props.project.boards);
  }, [type, project, props.project]);

  const onSelectBoard = board => _ => {
    console.log(board);

    let newItemsBoard = board.itemsBoards.map(item => {
      if (item.photos.length === 0) {
        item.photos = [{
          url: 'default.png',
          type: 'remote'
        }];
      }

      return item
    });
    board.itemsBoards = newItemsBoard;
    setBoard(board);

    let tmpCellsBefore = board.itemsBoards.filter(itemBoard => itemBoard.item.mode == 'before')
    setCellsBefore(tmpCellsBefore);

    let tmpCellsVoltaje = board.itemsBoards.filter(itemBoard => itemBoard.item.type == 'voltaje')
    setCellsVoltaje(tmpCellsVoltaje);

    let tmpCellsCorriente = board.itemsBoards.filter(itemBoard => itemBoard.item.type == 'corriente')
    setCellsCorriente(tmpCellsCorriente);

    let tmpCellsAfter = board.itemsBoards.filter(itemBoard => itemBoard.item.mode == 'after')
    setCellsAfter(tmpCellsAfter);
  }

  const showImages = url => _ => {
    console.log("showImages", url);
    setUrl(url);
    setOpenDetailImage(true);
  }
  const onOpenItemBoard = currentCell => _ => {
    console.log('currentCell', currentCell)
    openItemBoard(currentCell._id).then(response => {

      if (response.status === 'success') {
        switch (currentCell.item.mode) {
          case 'before':
            let tmpCellsBefore = cellsBefore.map(cell => {
              console.log('cell ', cell)
              if (cell._id === currentCell._id) {
                cell.status = 'activo';
              }
              return cell;
            })
            setCellsBefore(tmpCellsBefore);
            break;
          case 'after':
            let tmpCellsAfter = cellsAfter.map(cell => {
              if (cell._id === currentCell._id) {
                cell.status = 'activo';
              }
              return cell;
            })
            setCellsAfter(tmpCellsAfter)
            break;
        }
        switch (currentCell.item.type) {
          case 'voltaje':
            let tmpCellsVoltaje = cellsBefore.map(cell => {
              if (cell._id === currentCell._id) {
                cell.status = 'activo';
              }
              return cell;
            })
            setCellsVoltaje(tmpCellsVoltaje);
            break;
          case 'corriente':
            let tmpCellsCorriente = cellsBefore.map(cell => {
              if (cell._id === currentCell._id) {
                cell.status = 'activo';
              }
              return cell;
            })
            setCellsCorriente(tmpCellsCorriente);
            break;
        }
      }
    });
  }

  const sendEmailBoard = board => _ => {

    onSendEmailBoard(board._id).then(response => {
      console.log(response);
    })
  }

  const closeDatailImage = () => {
    setOpenDetailImage(false)
  }

  const classes = useStyles()

  return (
    <Card>
      <CardHeader title="TABLEROS" />
      <Divider />
      <CardContent className={classes.content}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>
                <div className='board__item__wrapper'>
                  {
                    project ?
                      project.boards.map((board, i) => {
                        return (<div key={i} className='board_item'>
                          <Chip
                            size='small'
                            color='primary'
                            label={board.name}
                            variant="outlined"
                            onClick={onSelectBoard(board)} />
                        </div>)
                      })
                      : ''
                  }
                </div>
              </TableCell>
            </TableRow>

            <TableRow>
              <TableCell>
                <div className='project__wrapper'>
                  <div className='project__info'>
                    {
                      project ?
                        <Fragment>
                          <div className='project_name'>{project.name}</div>
                          <div className='project_type'>{project.type === 'tri' ? 'Trif谩sico' : 'Monof谩sico'}</div>
                          <div className='board_name'>{board ? board.name : ''}</div>
                          <div className='board_email'>{board ? <AiOutlineMail className='icon' onClick={sendEmailBoard(board)} /> : ''}</div>
                        </Fragment>
                        : ''
                    }
                  </div>
                  <div className='project__board__wrapper'>
                    <Fragment>
                      {
                        cellsBefore.length > 0 ?
                          <Fragment>
                            <div className='title_action'>IMAGENES ANTES DE MANTENIMIENTO</div>
                            <div className='board__item__wrapper'>

                              {
                                cellsBefore.map((itemBefore, i) => {
                                  return (
                                    <div className="card" key={i}>
                                      <div className='card_title'>{itemBefore.item.title}</div>
                                      {
                                        itemBefore.item.letterOne !== '' ?
                                          <div className='card_colors'>
                                            <div className='card_color_left' style={{ backgroundColor: `#FCF338` }}>{itemBefore.item.colorOne}</div>
                                            <div className='card_color_right'></div>
                                          </div>
                                          : ''
                                      }
                                      <div className='card_header'>
                                        {
                                          itemBefore.status === 'inactivo'
                                            ?
                                            <div className='show__wrapper'>
                                              <BiShowAlt className='icon' onClick={onOpenItemBoard(itemBefore)} />
                                              <div className='show_text'>Esta marcaci贸n se encuentra oculta pulse en el icono para mostrar</div>
                                            </div>
                                            : ''
                                        }
                                      </div>
                                      <div className='card_body'>
                                        <div className="slide-container">
                                          {
                                            itemBefore.photos.length > 0 ?
                                              <Zoom>
                                                {
                                                  itemBefore.photos.map((photo, index) =>
                                                    <img key={index} style={{ width: "100%" }} src={`${config.urlImages}${photo.url}`} onClick={showImages(`${config.urlImages}${photo.url}`)} />
                                                  )
                                                }
                                              </Zoom> : ''
                                          }
                                        </div>
                                      </div>
                                    </div>
                                  )
                                })
                              }

                            </div>
                          </Fragment> : ''
                      }
                      {
                        cellsVoltaje.length > 0 ?
                          <Fragment>
                            <div className='title_action'>VOLTAJES</div>
                            <div className='board__item__wrapper'>

                              {
                                cellsVoltaje.map((itemVoltaje, i) => {
                                  return (
                                    <div className="card" key={i}>
                                      <div className='card_title'>{itemVoltaje.item.title}</div>
                                      {
                                        itemVoltaje.item.letterOne !== '' ?
                                          <div className='card_colors'>
                                            <div
                                              className='card_color_left'
                                              style={{
                                                backgroundColor: `#${itemVoltaje.item.colorOne}`,
                                                color: `#${itemVoltaje.item.textColorOne}`
                                              }}
                                            >
                                              {itemVoltaje.item.letterOne}
                                            </div>
                                            <div
                                              className='card_color_right'
                                              style={{
                                                backgroundColor: `#${itemVoltaje.item.colorTwo}`,
                                                color: `#${itemVoltaje.item.textColorTwo}`
                                              }}
                                            >
                                              {itemVoltaje.item.letterTwo}
                                            </div>
                                          </div>
                                          : ''
                                      }
                                      <div className='card_header'>
                                        {
                                          itemVoltaje.status === 'inactivo'
                                            ?
                                            <div className='show__wrapper'>
                                              <BiShowAlt className='icon' onClick={onOpenItemBoard(itemVoltaje)} />
                                              <div className='show_text'>Esta marcaci贸n se encuentra oculta pulse en el icono para mostrar</div>
                                            </div>
                                            : ''
                                        }
                                      </div>
                                      <div className='card_body'>
                                        <div className="slide-container">
                                          {
                                            itemVoltaje.photos.length > 0 ?
                                              <Zoom>
                                                {
                                                  itemVoltaje.photos.map((photo, index) =>
                                                    <img key={index} style={{ width: "100%" }} src={`${config.urlImages}${photo.url}`} onClick={showImages(`${config.urlImages}${photo.url}`)} />
                                                  )
                                                }
                                              </Zoom>
                                              : ''
                                          }
                                        </div>
                                      </div>
                                      {
                                        <div className='card_footer'>
                                          <span className='text_value'>Valor:</span>
                                          {itemVoltaje.item.value}
                                        </div>
                                      }
                                    </div>
                                  )
                                })
                              }

                            </div>
                          </Fragment> : ''
                      }
                    </Fragment>
                  </div>

                </div>

              </TableCell>
            </TableRow>
          </TableBody>
        </Table >
      </CardContent >
      <Dialog
        fullWidth maxWidth="sm"
        open={openDetailImage}
        onClose={closeDatailImage}
        TransitionComponent={Transition}>
        <DialogContent>
          <ImageDetail url={url} />
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={closeDatailImage} color="primary">
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>

    </Card >
  )
}

export default AttentionDetail