import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import BoardDetail from './BoardDetail';
import ProjectForm from './ProjectForm';
import { Button } from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';

import Tooltip from '@material-ui/core/Tooltip'
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { listProjects, deleteProject } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './projects.css';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})

const Projects = _ => {

    useEffect(() => {
        listProjects().then(response => {
            setProjects(response.projects);
            setTotalProjects(response.count);
        });
    }, []);

    const classes = useStyles()

    const [projects, setProjects] = useState([])
    const [project, setProject] = useState(null)
    const [totalProjects, setTotalProjects] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openDetailBoards, setOpenDetailBoards] = useState(false);
    const [openProjectForm, setOpenProjectForm] = useState(false);

    const handleChangePage = (_, newPage) => {
        setPage(newPage);
        // getPersons({ variables: { start: newPage, end: rowsPerPage } })
    }

    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        // getPersons({ variables: { start: 0, end: event.target.value } })
    }

    const onCloseDeleteProject = () => {
        setOpen(false);
    }

    const onDeleteProject = id => {
        setOpen(false);

        deleteProject(id).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newProjects = projects.map((currentProject) => {
                    if (id !== currentProject._id)
                        return currentProject

                    currentProject.status = "deleted";
                    return currentProject
                })
                setProjects(newProjects)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurri贸 un error al eliminar el proyecto', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }

    const onDetailBoard = project => () => {
        setProject(project)
        setOpenDetailBoards(true)
    }

    const closeDatailBoards = () => {
        setOpenDetailBoards(false)
    }

    const onOpenProjectForm = () => {
        setOpenProjectForm(true)
    }

    const onCloseProjectForm = () => {
        setOpenProjectForm(false)
    }



    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Fab onClick={onOpenProjectForm} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                    Nuevo proyecto
                </Fab>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>DESCRIPCIN</TableCell>
                            <TableCell>CLIENTE</TableCell>
                            <TableCell>TABLEROS</TableCell>
                            <TableCell>VER</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            projects.map(item => {
                                return (
                                    <TableRow key={item._id}>
                                        <TableCell>{item.name} </TableCell>
                                        <TableCell>{item.customer.name}</TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Chip size='small' label={item.boards.length} variant="outlined" />
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailBoard(item)} />
                                            </Tooltip>
                                        </TableCell>

                                    </TableRow>)
                            })
                        }
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalProjects}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>

            <Dialog
                open={open}
                onClose={onCloseDeleteProject}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta atenci贸n?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteProject} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteProject} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openProjectForm} onClose={onCloseProjectForm} TransitionComponent={Transition}>
                <DialogContent>
                    <ProjectForm onClose={onCloseProjectForm} project={project} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseProjectForm} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth maxWidth="lg"
                open={openDetailBoards}
                onClose={closeDatailBoards}
                TransitionComponent={Transition}>
                <DialogContent>
                    <BoardDetail project={project} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDatailBoards} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

        </Fragment>)
}

export default Projects